<template lang="html">
  <div class="markdown-body" v-html="html"></div>
</template>

<script>
import marked from 'marked'

marked.setOptions({
  // renderer: new marked.Renderer(),
  // gfm: true,
  // tables: true,
  // breaks: false,
  // pedantic: false,
  // sanitize: true,
  // smartLists: true,
  // smartypants: false,
  highlight (code) {
    return require('highlight.js').highlightAuto(code).value
  }
})

export default {
  name: 'hd-markdown',
  props: {
    lang: String,
    content: {
      type: String,
      required: true
    }
  },
  computed: {
    html () {
      let content = this.content
      let lang = this.lang
      if (lang) {
        content = '```' + lang + '\n' + content + '\n```'
      }
      return marked(content)
    }
  }
}
</script>

<style lang="pcss">
@import "./css/github-markdown.css";
@import "./css/github-gist.css";
</style>



