<template lang="html">
  <div class="hd-demo">
    <div class="hd-demo-example">
      <slot></slot>
    </div>
    <div class="hd-demo-info">
      <h3 class="hd-demo-info--title" v-text="title"></h3>
      <p class="hd-demo-info--desc" v-text="desc"></p>
      <a href="javascript:;" class="hd-demo-info--show" @click="showCode" v-text="(!show ? 'show' : 'hide') + ' code'"></a>
    </div>
    <markdown v-show="show" class="hd-demo-code" lang="javascript" :content="code"></markdown>
  </div>
</template>

<script>
import Markdown from './markdown.vue'

export default {
  name: 'hd-demo',
  components: {
    Markdown
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    desc: {
      type: String,
      default: ''
    },
    code: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      show: false
    }
  },
  methods: {
    showCode () {
      this.show = !this.show
    }
  }
}
</script>

<style lang="pcss">
@namespace hd {
  @b demo {
    font-size: 14px;
    line-height: 1.5;
    border: 1px solid #eee;
    border-radius: 4px;
    transition: all .2s ease;
    margin-bottom: 30px;
    overflow: hidden;

    &:hover {
      box-shadow: 0 2px 7px rgba(0, 0, 0, .2);
    }

    @e example {
      padding: 16px;
      border-bottom: 1px solid #eee;
    }

    @e info {
      position: relative;
      top: 1px;
      border-bottom: 1px dashed #eee;
      padding: 20px;

      @m title {
        font-size: 16px;
        color: #333;
        background-color: #fff;
        margin-bottom: 10px;
      }

      @m desc {
        font-size: 12px;
        color: #666;
        margin-bottom: 12px;
      }

      @m show {
        font-size: 24px;
        position: absolute;
        right: 20px;
        bottom: 12px;
      }
    }

    @e code {
      padding: 16px;
    }
  }
}
</style>



